:root {
  --primary-gray: #7f8c8d;
  --primary-white: #ecf0f1;
  --primary-dark: #34495e;
  --primary-deep-dark: #2c3e50;
  --primary-color: #8e44ad;
  --primary-color-hover: #9b59b6;

  --font-family: "Inria Sans", sans-serif;

  --radius: 4px;
  --element-padding: 0.5rem 1rem;
  --element-padding-square: 0.5rem;

  --transition-250: 250ms ease-in-out;
}
